import '../../../libs/slider/jquery-ui.min';
import '../../../libs/popup/jquery.magnific-popup.min';
import '../../../libs/touch/touch';
import countries from '../../../data/countries';

$(document).ready(function () {

  $(".card-favorite__btn").on("click", function () {
    $(this).parent().toggleClass("active");
  });

  // popup close

  $('.mfp-close').on('click', function () {
    var magnificPopup = $.magnificPopup.instance;
    magnificPopup.close();
  });

  // Select sort by

  const Select = function (node) {

    const self = this;
    self.node = node;
    self.list = [];
    self.text = "";

    const listItems = self.node.querySelectorAll(".form-select__item");

    self.init = function () {
      self.list = self.node.querySelector(".form-select__list");
      self.text = self.node.querySelector(".form-select__head");

      self.addListeners();
    };

    self.addListeners = function () {

      listItems.forEach(function (item) {
        item.addEventListener("click", function (event) {
          event.preventDefault();
          self.changeText(event.target);
        });
      });

      self.node.addEventListener('click', function (event) {
        self.open(event.target);
      });
    };

    self.open = function (node) {
      event.target.classList.toggle("arrow-up");
      if(node.nextElementSibling !== null) {
        node.nextElementSibling.classList.toggle("open");
      }
    };

    self.close = function () {
      self.list.classList.remove("open");
      self.text.classList.remove("arrow-up");
    };

    self.changeText = function (node) {
      self.text.textContent = node.textContent;
      self.close();
    };
  };

  const selectArr = document.querySelectorAll("[data-type='select']");
  for (let i = 0; i < selectArr.length; i++) {
    const select = new Select(selectArr[i]);
    select.init();
  }

  $(document).on('click', function (e) {
    if(!$(e.target).closest("[data-type='select']").length) {
      $(".form-select__list").removeClass("open");
      $(".form-select__head").removeClass("arrow-up");
    }
    e.stopPropagation();
  });

  //  toggle

  $(".filter-button").click(function () {
    const self = $(this);
    self.toggleClass("open");
    self.next().slideToggle(300);
  });

  // range slider price

  /*
  * data-gfrom - min 
  * data-gto - max 
  * data-lfrom - min     
  * data-lto - max     
  * */

  $('#rangePrice').slider({
    range: true,
    step: 1000,
    min: $('#rangePrice').data('gfrom'),
    max: $('#rangePrice').data('gto'),
    values: [$('#rangePrice').data('lfrom'), $('#rangePrice').data('lto')],
    slide: function (event, ui) {
      $('#rangeFromPrice').val(ui.values[0]);
      $('#rangeToPrice').val(ui.values[1]);
    }
  });

  $('#rangeFromPrice').val($('#rangePrice').slider('values', 0));
  $('#rangeToPrice').val($('#rangePrice').slider('values', 1));

  // range slider year

  $('#rangeYear').slider({
    range: true,
    step: 1,
    min: $('#rangeYear').data('gfrom'),
    max: $('#rangeYear').data('gto'),
    values: [$('#rangeYear').data('lfrom'), $('#rangeYear').data('lto')],
    slide: function (event, ui) {
      $('#rangeFromYear').val(ui.values[0]);
      $('#rangeToYear').val(ui.values[1]);
    }
  });

  $('#rangeFromYear').val($('#rangeYear').slider('values', 0));
  $('#rangeToYear').val($('#rangeYear').slider('values', 1));

  //  range slider length

  $('#rangeLength').slider({
    range: true,
    step: 1,
    min: $('#rangeLength').data('gfrom'),
    max: $('#rangeLength').data('gto'),
    values: [$('#rangeLength').data('lfrom'), $('#rangeLength').data('lto')],
    slide: function (event, ui) {
      $('#rangeFromLength').val(`${ (ui.values[0] * 3.2808).toFixed(0) }ft/${ ui.values[0] }m`);
      $('#rangeToLength').val(`${ (ui.values[1] * 3.2808).toFixed(0) }ft/${ ui.values[1] }m`);
    }
  });

  $('#rangeFromLength').val(`${ ($('#rangeLength').slider('values', 0) * 3.2808).toFixed(0) }ft/${ $('#rangeLength').slider('values', 0) }m`);
  $('#rangeToLength').val(`${ ($('#rangeLength').slider('values', 1) * 3.2808).toFixed(0) }ft/${ $('#rangeLength').slider('values', 1) }m`);


  //  

  $('#rangePrice2').slider({
    range: true,
    step: 1000,
    min: $('#rangePrice2').data('gfrom'),
    max: $('#rangePrice2').data('gto'),
    values: [$('#rangePrice2').data('lfrom'), $('#rangePrice2').data('lto')],
    slide: function (event, ui) {
      $('#rangeFromPrice2').val(ui.values[0]);
      $('#rangeToPrice2').val(ui.values[1]);
    }
  });

  $('#rangeFromPrice2').val($('#rangePrice2').slider('values', 0));
  $('#rangeToPrice2').val($('#rangePrice2').slider('values', 1));

  // range slider year

  $('#rangeYear2').slider({
    range: true,
    step: 1,
    min: $('#rangeYear2').data('gfrom'),
    max: $('#rangeYear2').data('gto'),
    values: [$('#rangeYear2').data('lfrom'), $('#rangeYear2').data('lto')],
    slide: function (event, ui) {
      $('#rangeFromYear2').val(ui.values[0]);
      $('#rangeToYear2').val(ui.values[1]);
    }
  });

  $('#rangeFromYear2').val($('#rangeYear2').slider('values', 0));
  $('#rangeToYear2').val($('#rangeYear2').slider('values', 1));

  //  range slider length

  $('#rangeLength2').slider({
    range: true,
    step: 1,
    min: $('#rangeLength2').data('gfrom'),
    max: $('#rangeLength2').data('gto'),
    values: [$('#rangeLength2').data('lfrom'), $('#rangeLength2').data('lto')],
    slide: function (event, ui) {
      $('#rangeFromLength2').val(`${ (ui.values[0] * 3.2808).toFixed(0) }ft/${ ui.values[0] }m`);
      $('#rangeToLength2').val(`${ (ui.values[1] * 3.2808).toFixed(0) }ft/${ ui.values[1] }m`);
    }
  });

  $('#rangeFromLength2').val(`${ ($('#rangeLength2').slider('values', 0) * 3.2808).toFixed(0) }ft/${ $('#rangeLength2').slider('values', 0) }m`);
  $('#rangeToLength2').val(`${ ($('#rangeLength2').slider('values', 1) * 3.2808).toFixed(0) }ft/${ $('#rangeLength2').slider('values', 1) }m`);

  //num +1

  const addBasket = function (node) {
    const self = this;
    self.node = node;

    const plus = self.node.querySelector("[data-plus]");
    const minus = self.node.querySelector("[data-minus]");
    const sum = self.node.querySelector("[data-sum]");


    self.init = function () {
      self.plus();
      self.minus();
    };

    self.plus = function () {
      plus.addEventListener("click", function () {
        if(+sum.textContent >= 0) {
          sum.textContent = +sum.textContent + 1;
        }
      });
    };

    self.minus = function () {
      minus.addEventListener("click", function () {
        if(+sum.textContent >= 1) {
          sum.textContent = +sum.textContent - 1;
        }
      });
    };
  };

  const basket = document.querySelectorAll("[data-add]");

  for (let i = 0; i < basket.length; i++) {
    const add = new addBasket(basket[i]);
    add.init();
  }

  //  toggle map
  $(".open-map-js").click(function () {
    $(".catalog-map").toggleClass("open");
  });

  //  data picker
  $(function () {
    let dateFormat = "Medium";

    let from = $("#from")
      .datepicker({
        defaultDate: "11 Feb, 2020",
        dateFormat: "d M, yy",
        changeMonth: false,
        numberOfMonths: 1
      })
      .on("change", function () {
        to.datepicker("option", "minDate", getDate(this));
      });
    let to = $("#to").datepicker({
      defaultDate: "11 Feb, 2021",
      dateFormat: "d M, yy",
      changeMonth: false,
      numberOfMonths: 1
    })
      .on("change", function () {
        from.datepicker("option", "maxDate", getDate(this));
      });

    function getDate(element) {
      let date;
      try {
        date = $.datepicker.parseDate(dateFormat, element.value);
      } catch (error) {
        date = null;
      }

      return date;
    }
  });

  // Search

  const input = document.querySelector("#search-field");
  const list = document.querySelector("#search-list");

  input.addEventListener("keyup", complete);
  list.addEventListener("click", selectWord);

  function complete() {
    const val = input.value.trim().toLocaleLowerCase();
    list.style.display = "block";
    if(val.length > 1) {
      const country = countries.filter(county => county.name.toLocaleLowerCase().indexOf(val) === 0);

      if(country.length >= 1) {
        list.innerHTML = renderHtml(country);
      } else {
        list.innerHTML = `<li class="form-select__item"><span>No results...</span></li>`;
      }
    } else {
      list.innerHTML = `<li class="form-select__item"><span>The list is too short...</span></li>`;
    }
  }

  function selectWord(e) {
    e.preventDefault();
    const target = e.target;
    if(target.parentNode.nodeName === "LI") {
      input.value = target.innerHTML;
      list.style.display = "none";
    }
  }

  function renderHtml(arr) {
    let html = "";
    for (let i = 0; i < arr.length; i++) {
      html += `<li class="form-select__item"><a href="#">${arr[i].name}</a></li>`
    }

    return html;
  }

  $(document).on('click', function (e) {
    if(!$(e.target).closest(".search").length) {
      $("#search-list").css("display", "none");
    }
    e.stopPropagation();
  });

//  popup
  $('.popup-with-zoom-anim').magnificPopup({
    type: 'inline',

    fixedContentPos: false,
    fixedBgPos: true,

    overflowY: 'auto',

    closeBtnInside: true,
    preloader: false,

    midClick: true,
    removalDelay: 300,
    mainClass: 'my-mfp-zoom-in'
  });

});
